	private makeSalutation(): string {
		let un = this.globals.userdata.general.FirstName;
		un = un ? un : "";

		let ln = this.globals.userdata.general.LastName;
		ln = ln ? ln : "";

		let userName = un + " " + ln;

		if (userName.length === 0) {
			userName = "Wealth Builder";
		}
		return userName;
	}



